.link-icon {
  display: inline-block;
  width: 2.5rem;
  height: 2.5rem; }
  .link-icon svg {
    fill: #aaa;
    width: 1.5rem;
    height: 1.5rem;
    margin: 0.5rem; }
  .link-icon:active svg, .link-icon:focus svg, .link-icon:hover svg {
    fill: #777;
    -webkit-transition: fill 200ms ease-in;
    -o-transition: fill 200ms ease-in;
    transition: fill 200ms ease-in; }



