/* Modal animation */
.overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: transparent; }
  .overlay--after-open {
    background-color: rgba(50, 50, 50, 0.5);
    -webkit-transition: background-color 300ms ease-in;
    -o-transition: background-color 300ms ease-in;
    transition: background-color 300ms ease-in; }
  .overlay--before-close {
    background-color: transparent;
    -webkit-transition: top 300ms ease-in;
    -o-transition: top 300ms ease-in;
    transition: top 300ms ease-in; }

.about {
  position: absolute;
  top: -100%;
  width: 100%;
  min-height: 100%;
  outline: 0;
  padding: 2rem 1rem 5rem;
  background-color: #eee; }
  .about--after-open {
    top: 0;
    -webkit-transition: top 300ms ease-in;
    -o-transition: top 300ms ease-in;
    transition: top 300ms ease-in; }
  .about--before-close {
    top: -100%;
    -webkit-transition: top 300ms ease-in;
    -o-transition: top 300ms ease-in;
    transition: top 300ms ease-in; }
  .about__header {
    margin-bottom: 2rem; }
    .about__header__title {
      margin-bottom: 1rem;
      font-size: 2rem; }
  .about__block {
    margin-bottom: 2rem; }
    .about__block__title {
      margin-bottom: 1rem;
      font-size: 1.5rem; }
    .about__block__list {
      padding-left: 1rem;
      text-align: left; }
      .about__block__list__item {
        margin-bottom: 0.5rem; }
        .about__block__list__item__example {
          font-size: 0.9rem;
          font-style: italic; }
    .about__block__text {
      text-align: left; }
      .about__block__text__link {
        color: #444; }
  .about__button-close {
    position: absolute;
    top: 0.5rem;
    right: 0.5rem;
    display: block;
    width: 2.5rem;
    height: 2.5rem;
    background-color: #eee;
    font-size: 1.5rem; }
  .about__footer {
    position: absolute;
    bottom: 1rem;
    font-size: 0.7rem; }
  @media (min-width: 48rem) {
    .about {
      left: 25%;
      width: 50%;
      min-height: auto; }
      .about--after-open {
        top: 10rem;
        -webkit-transition: top 300ms ease-in;
        -o-transition: top 300ms ease-in;
        transition: top 300ms ease-in; }
      .about--before-close {
        top: -100%;
        -webkit-transition: top 300ms ease-in;
        -o-transition: top 300ms ease-in;
        transition: top 300ms ease-in; } }
  @media (min-width: 75rem) {
    .about {
      left: 50%;
      width: 37.5rem;
      margin-left: -18.75rem; } }



