/* Router animation */
/* Need more specificity to override home-page/form-page/matches-page styles */
.form-page.route-enter,
.home-page.route-enter,
.matches-page.route-enter {
  position: absolute;
  top: 50%;
  opacity: 0; }
  .form-page.route-enter.route-enter-active,
  .home-page.route-enter.route-enter-active,
  .matches-page.route-enter.route-enter-active {
    top: 0;
    opacity: 1;
    -webkit-transition: top 300ms ease-in 300ms, opacity 300ms ease-in 300ms;
    -o-transition: top 300ms ease-in 300ms, opacity 300ms ease-in 300ms;
    transition: top 300ms ease-in 300ms, opacity 300ms ease-in 300ms; }

.form-page.route-exit,
.home-page.route-exit,
.matches-page.route-exit {
  position: absolute;
  top: 0;
  opacity: 1; }
  .form-page.route-exit.route-exit-active,
  .home-page.route-exit.route-exit-active,
  .matches-page.route-exit.route-exit-active {
    top: -50%;
    opacity: 0;
    -webkit-transition: top 300ms ease-in, opacity 300ms ease-in;
    -o-transition: top 300ms ease-in, opacity 300ms ease-in;
    transition: top 300ms ease-in, opacity 300ms ease-in; }



